@import "../styles/variables";

$header-height: 72px;
$footer-height: 120px;
$content-max-width: 480px;

.headerContainer {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.header {
  width: 100%;
  max-width: $content-max-width;
  height: $header-height;
  background: #1E2563;
  padding-right: $space-2;
  padding-left: $space-2;
  display: flex;
  align-items: center;
}

.contentContainer {
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

.content {
  width: 100%;
  max-width: $content-max-width;
  background: $main-gradient;
  padding: calc(#{$header-height} + #{$space-3}) $space-2 calc(#{$footer-height} + #{$space-2});
}

.footerContainer {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.footer {
  width: 100%;
  max-width: $content-max-width;
  height: $footer-height;
  background: $footer-gradient;
  padding-right: $space-2;
  padding-left: $space-2;
  display: flex;
  justify-content: center;
  align-items: center;
}
