@import "fonts";
@import "variables";

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  margin: 0;
  background: $dark-gradient;
  color: #ffffff;
  font-family: $raleway;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.05em;
}

p {
  margin: 0;
}

a {
  font-weight: 700;
  color: #FFDE89;
  text-decoration: none;
}
