@import "../styles/variables";

.textInput {
  background: rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  width: 100%;
  padding: $space-2;
  border: 2px solid transparent;
  box-shadow: none;
  font-size: 18px;
  line-height: 16px;
  font-family: $pt-mono;
  font-weight: 700;
  color: #ffffff;

  &:focus {
    border-color: #FFDE89;
    outline: none;
  }
}

.hasErrors {
  border-color: #FF8989;
}

.label {
  font-weight: 700;
  text-transform: uppercase;
  color: #ABA5C5;
  display:inline-block;
  margin-bottom: $space-1;
}
