@import "../styles/variables";

.titleContainer {
  display: flex;
  margin-top: $space-3;
  margin-bottom: $space-3;
}

.icon {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  margin-right: 12px;
}

.title {
  font-weight: 600;
  font-size: 28px;
  line-height: 40px;
  color: #ffffff;
}
