@import "../styles/variables";

.checkboxContainer {
  display: flex;
  align-items: center;

  input:checked ~ .checkbox {
    background-color: #FFDE89;

    &:after {
      display: block;
    }
  }
}

.nativeCheckbox {
  width: 0;
  height: 0;
  margin: 0;
}

.label {
  font-weight: 700;
}

.checkbox {
  display: block;
  width: 18px;
  height: 18px;
  position: relative;
  border: 2px solid #FFDE89;
  border-radius: 2px;
  margin-right: $space-1;
  transition: all 300ms;

  &:after {
    content: "";
    position: absolute;
    display: none;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -60%) rotate(45deg);
    width: 7px;
    height: 11px;
    border: solid #1E2563;
    border-width: 0 3px 3px 0;
  }
}

.hasErrors {
  border-color: #FF8989;
}
